import React from "react";
import request from "../helpers/request";
import Loader from "../components/Loader";
import {
  Icon,
  Grid,
  Table,
  TableRow,
  TableCell,
  TableBody,
  TableHead,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField
} from "@material-ui/core";

const RenderItems = ({ items = [], renderItem }) => items.map(renderItem);

class Subcuentas extends React.Component {
  params = this.props.navigation.params();
  state = {
    subaccounts: [],
    loading: true,
    openDialog: false,
    selectedAccount: {}
  };

  componentDidMount() {
    this.GetAccounts();
  }

  async GetAccounts() {
    try {
      const result = await request.get(
        `/subcuentas?code=${this.params.accountCode}&segmento=${this.params.projectCode}&idCuenta=${this.params.accountId}`
      );
      // console.log(this.params)
      if (result.error) {
        alert("Favor de revisar error en la consola");
        console.log(result.message);
      }
      this.setState({
        subaccounts: result.data,
        loading: false
      });
    } catch (message) {
      alert(message);
      this.setState({
        subaccounts: [],
        loading: false
      });
    }
  }

  async selectItem(index) {
    const { subaccounts } = this.state;
    const selectedAccount = subaccounts[index];

    const result = await request.get(
      `/tiene-presupuesto?segmento=${this.params.projectCode}&idCuentaPadre=${this.params.accountId}&cuenta=${selectedAccount.codigo}`
    );

    if (result.error) {
      alert("Ocurrio un error, revisar en la consola");
      console.log(result.message);
    } else {
      selectedAccount.idPresupuestoCuenta = result.data.id;
      selectedAccount.existe = result.data.exist;
      this.setState({ selectedAccount, openDialog: true });
    }
  }

  handleClose = () => {
    this.setState({ openDialog: false, selectedAccount: {} })
  }
  updateItem = async () => {
    const { selectedAccount } = this.state;
    const data = {
      existe: selectedAccount.existe,
      idCuentaPadre: this.params.accountId,
      cuenta: selectedAccount.codigo,
      segmento: this.params.projectCode,
      id: selectedAccount.idPresupuestoCuenta,
      total: selectedAccount.presupuesto
    };
    await request.post("/agregar-presupuesto", data);
    this.GetAccounts();
    this.handleClose();
  };

  handleChange = ev => {
    const { value } = ev.target;
    const { selectedAccount } = this.state;
    selectedAccount.presupuesto = parseFloat(value);
    this.setState({ selectedAccount });
  };

  render() {
    const { subaccounts, loading, openDialog, selectedAccount } = this.state;
    return (
      <Grid>
        <Loader show={loading} />
        <Dialog
          open={openDialog}
          onClose={this.handleClose}
        >
          <DialogTitle>{selectedAccount.nombre || "Titulo"}</DialogTitle>
          <DialogContent>
            <TextField
              type="number"
              label="Presupuesto"
              value={selectedAccount.presupuesto || 0}
              onChange={this.handleChange}
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() =>
                this.setState({ openDialog: false, selectedAccount: {} })
              }
              color="secondary"
            >
              Cancelar
            </Button>
            <Button onClick={this.updateItem} color="primary" autoFocus>
              Actualizar
            </Button>
          </DialogActions>
        </Dialog>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Codigo</TableCell>
              <TableCell>Nombre</TableCell>
              <TableCell>Presupuesto</TableCell>
              <TableCell>Gastado</TableCell>
              <TableCell>Resta</TableCell>
              <TableCell>Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <RenderItems
              items={subaccounts}
              renderItem={(item, i) => (
                <TableRow key={item.id}>
                  <TableCell>{item.codigo}</TableCell>
                  <TableCell>{item.nombre}</TableCell>
                  <TableCell>
                    $
                    {item.presupuesto.toLocaleString("es-Mx", {
                      maximumFractionDigits: 2
                    })}
                  </TableCell>
                  <TableCell>
                    $
                    {item.ordenesCompra.toLocaleString("es-Mx", {
                      maximumFractionDigits: 2
                    })}
                  </TableCell>
                  <TableCell>
                    $
                    {(item.presupuesto - item.ordenesCompra).toLocaleString(
                      "es-Mx",
                      {
                        maximumFractionDigits: 2
                      }
                    )}
                  </TableCell>
                  <TableCell>
                    <Button
                      onClick={() =>
                        this.props.navigation.go("DetalleCuentas", {
                          cuenta: item.codigo,
                          projectId: this.params.projectId,
                          projectCode: this.params.projectCode,
                          projectName: this.params.projectName
                        })
                      }
                    >
                      <Icon>format_align_justify</Icon>
                    </Button>
                    <Button onClick={() => this.selectItem(i)}>
                      <Icon>create</Icon>
                    </Button>
                  </TableCell>
                </TableRow>
              )}
            />
          </TableBody>
        </Table>
      </Grid>
    );
  }
}

export default Subcuentas;
